*{
  margin: 0;
  padding: 0;
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
}
p{
  margin-bottom: 8px;
}
header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  background-color:  #ffffff;
}
header a{
  font-size: 50px;
  color: #707070;

}
header a:hover{
  text-decoration: none;
}
nav{
  height: 149px;
  line-height: 149px;
  text-align: right;
}
nav ul{
  display: flex;
}
nav ul li{
  list-style: none;

  padding: 0;
  margin: 0 1vw;
}
nav ul li a{
  font-size: 35px;
  color: #192250;
  font-weight: 300;
  text-transform: uppercase;
}
nav ul li a:hover{
  text-decoration: none;
}
nav ul li:nth-child(1) a{
  color: #AFB4D1;
}


.myBox {
  background-image: url("../src/bg.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  height: 820px;
  border-top: 18px solid #932A8A;
  position: relative;
}
.loadBox{
  width: 400px;
  height: 170px;
  line-height: 170px;
  font-size: 50px;

  text-align: center;
  color: #707070;
  background-color: white;
  position: absolute;
  top: 50%;
  left:40%;
  font-family: 'Roboto', sans-serif;
  font-weight: 100;
}
.col-sm-6{
  padding:0;
  position: relative;
}
.image-box{
  text-align: right;
  margin-top: 10px;

}
.image-box img{

  width: 435px;
}
.bio-box{
  background-color: #ffffff;
  width: 450px;
  height: 450px;
  position: absolute;
  top: 50px;
  padding-left: 30px;
  color: #707070;
}
.bio-box h1{
  font-size: 50px;
  letter-spacing: 0;
  margin: 0;
  font-weight: 300;
}
.bio-box h5{
  font-size: 20px;
  margin-bottom: 30px;
  font-weight: 300;
}

.bio-box p {
  position: relative;
  font-size: .9rem;
}
.bio-box span{
  position: absolute;
  right:5px;
}
.map-container{
  height: 140px;
}
.footer-box{
  height: 98px;
  line-height: 98px;
  background-color: #192250;
  border-top: 1px solid #707070;
  text-align: center;
  position: absolute;
  bottom:0;
  left:0;
  right:0
}
.footer-box a i {
  color: white;
  font-size: 30px;
  margin: 0 .8vw;

}
